/* ============================================
   MEDIA CARD - Luxury Minimalist Design
   Venice-inspired  Luminous  Elegant
   ============================================ */

.media-card {
  position: relative;
  border-radius: var(--radius-xl);
  overflow: hidden;
  cursor: pointer;
  transition: all var(--transition-smooth);
  background: var(--bg-elevated);
  border: 1px solid var(--border-medium);
  flex-shrink: 0;
  will-change: transform;
  box-shadow:
    0 4px 16px rgba(0, 0, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.04);
}

.media-card::before {
  content: '';
  position: absolute;
  inset: -1px;
  border-radius: var(--radius-xl);
  padding: 1px;
  background: linear-gradient(135deg,
    rgba(0, 212, 184, 0.3) 0%,
    transparent 50%,
    rgba(0, 212, 184, 0.15) 100%
  );
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0;
  transition: opacity var(--transition-normal);
  z-index: 10;
}

.media-card:hover::before {
  opacity: 1;
}

.media-card:hover {
  transform: translateY(-8px);
  border-color: var(--color-primary);
  box-shadow:
    0 16px 48px rgba(0, 0, 0, 0.3),
    0 0 40px rgba(0, 212, 184, 0.12);
}

/* ============================================
   POSTER
   ============================================ */

.media-card__poster {
  width: 100%;
  aspect-ratio: 2/3;
  background: var(--bg-elevated-2);
  position: relative;
  overflow: hidden;
}

.media-card__poster img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--duration-slower) var(--ease-smooth);
}

.media-card:hover .media-card__poster img {
  transform: scale(1.05);
}

.media-card__placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
  background: linear-gradient(135deg,
    var(--gray-800) 0%,
    var(--gray-700) 100%
  );
  color: var(--text-tertiary);
}

/* ============================================
   OVERLAY - Subtle & Elegant
   ============================================ */

.media-card__overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.9) 0%,
    rgba(0, 0, 0, 0.6) 40%,
    transparent 100%
  );
  opacity: 0;
  transition: opacity var(--transition-normal);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: var(--space-6);
  pointer-events: none;
}

.media-card:hover .media-card__overlay {
  opacity: 1;
}

/* ============================================
   PLAY BUTTON - Luxury Icon
   ============================================ */

.media-card__play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.9);
  background: rgba(0, 212, 184, 0.95);
  backdrop-filter: blur(12px);
  border: none;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transition: all var(--transition-spring);
  color: var(--text-inverse);
  padding: 0;
  box-shadow:
    0 8px 24px rgba(0, 212, 184, 0.4),
    0 0 20px rgba(0, 212, 184, 0.3);
  pointer-events: all;
}

.media-card:hover .media-card__play-button {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.media-card__play-button:hover {
  transform: translate(-50%, -50%) scale(1.1);
  background: var(--color-primary-light);
  box-shadow:
    0 12px 32px rgba(0, 212, 184, 0.5),
    0 0 30px rgba(0, 212, 184, 0.4);
}

.media-card__play-button:active {
  transform: translate(-50%, -50%) scale(0.95);
}

.media-card__play-button svg {
  width: 28px;
  height: 28px;
  margin-left: 3px; /* Visual centering */
}

/* ============================================
   INFO SECTION - Clean & Spacious
   ============================================ */

.media-card__info {
  padding: var(--space-4);
  text-align: left;
  background: var(--bg-elevated);
  border-top: 1px solid var(--border-subtle);
}

.media-card__title-row {
  display: flex;
  align-items: flex-start;
  gap: var(--space-2);
  margin-bottom: var(--space-2);
}

.media-card__title {
  font-family: var(--font-display);
  font-size: var(--text-sm);
  font-weight: var(--weight-medium);
  letter-spacing: var(--tracking-wide);
  line-height: var(--leading-snug);
  color: var(--text-primary);
  flex: 1;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.media-card__meta {
  font-size: var(--text-xs);
  font-weight: var(--weight-regular);
  color: var(--text-tertiary);
  letter-spacing: var(--tracking-wide);
  display: flex;
  align-items: center;
  gap: var(--space-2);
}

/* ============================================
   BBC BADGE - Refined
   ============================================ */

.media-card__bbc-badge {
  position: absolute;
  top: var(--space-3);
  right: var(--space-3);
  padding: var(--space-1) var(--space-2);
  background: rgba(139, 0, 0, 0.95);
  backdrop-filter: blur(12px);
  border-radius: var(--radius-sm);
  font-size: 10px;
  font-weight: var(--weight-bold);
  letter-spacing: var(--tracking-widest);
  color: var(--text-primary);
  box-shadow: var(--shadow-md);
  z-index: 5;
  font-family: var(--font-mono);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* ============================================
   PROGRESS BAR - Sleek
   ============================================ */

.media-card__progress {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: transparent;
  z-index: 3;
}

.media-card__progress-bar {
  height: 100%;
  background: linear-gradient(
    90deg,
    var(--color-primary) 0%,
    var(--color-primary-light) 100%
  );
  transition: width var(--transition-normal);
  box-shadow: 0 0 12px rgba(0, 212, 184, 0.7);
}

/* ============================================
   RATING - Elegant Display
   ============================================ */

.media-card__rating {
  display: inline-flex;
  align-items: center;
  gap: var(--space-1);
  padding: var(--space-1) var(--space-2);
  background: var(--bg-glass);
  backdrop-filter: blur(8px);
  border-radius: var(--radius-sm);
  font-size: var(--text-xs);
  font-weight: var(--weight-semibold);
  color: var(--color-primary-light);
  border: 1px solid var(--border-light);
}

.media-card__rating svg {
  width: 12px;
  height: 12px;
  fill: var(--color-primary);
}

/* ============================================
   OVERLAY CONTENT - Venice-style Typography
   ============================================ */

.media-card__overlay-title {
  font-family: var(--font-display);
  font-size: var(--text-lg);
  font-weight: var(--weight-semibold);
  letter-spacing: var(--tracking-tight);
  line-height: var(--leading-tight);
  color: var(--white);
  margin-bottom: var(--space-2);
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.media-card__overlay-info {
  font-size: var(--text-sm);
  font-weight: var(--weight-regular);
  color: var(--gray-200);
  display: flex;
  align-items: center;
  gap: var(--space-3);
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

.media-card__overlay-info span {
  display: flex;
  align-items: center;
  gap: var(--space-1);
}

/* ============================================
   RESPONSIVE - Desktop vs Mobile
   ============================================ */

/* Desktop Enhancements */
@media (min-width: 1024px) {
  .media-card {
    border-radius: var(--radius-2xl);
  }

  .media-card:hover {
    transform: translateY(-12px);
  }

  .media-card__info {
    padding: var(--space-5);
  }

  .media-card__title {
    font-size: var(--text-base);
  }

  .media-card__play-button {
    width: 72px;
    height: 72px;
  }

  .media-card__play-button svg {
    width: 32px;
    height: 32px;
  }
}

/* Mobile Optimizations */
@media (max-width: 768px) {
  .media-card {
    border-radius: var(--radius-lg);
  }

  .media-card:hover {
    transform: translateY(-4px);
  }

  .media-card:active {
    transform: scale(0.98);
  }

  .media-card__info {
    padding: var(--space-3);
  }

  .media-card__title {
    font-size: var(--text-xs);
    -webkit-line-clamp: 1;
  }

  .media-card__meta {
    font-size: 10px;
  }

  .media-card__play-button {
    width: 48px;
    height: 48px;
    opacity: 0.95; /* Always slightly visible on mobile */
  }

  .media-card__play-button svg {
    width: 20px;
    height: 20px;
  }

  /* Mobile: Simplified overlay */
  .media-card__overlay {
    padding: var(--space-4);
    background: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.85) 0%,
      transparent 60%
    );
  }

  /* Mobile: Show overlay on tap */
  .media-card.tapped .media-card__overlay {
    opacity: 1;
  }
}

/* Tablet */
@media (min-width: 769px) and (max-width: 1023px) {
  .media-card__title {
    font-size: var(--text-sm);
  }

  .media-card__play-button {
    width: 56px;
    height: 56px;
  }

  .media-card__play-button svg {
    width: 24px;
    height: 24px;
  }
}

/* ============================================
   ACCESSIBILITY
   ============================================ */

.media-card:focus-visible {
  outline: 2px solid var(--color-primary);
  outline-offset: 4px;
}

.media-card__play-button:focus-visible {
  outline: 2px solid var(--white);
  outline-offset: 4px;
}

@media (prefers-reduced-motion: reduce) {
  .media-card,
  .media-card__poster img,
  .media-card__overlay,
  .media-card__play-button {
    transition: none;
  }

  .media-card:hover .media-card__poster img {
    transform: none;
  }
}

/* ============================================
   DARK MODE VARIATIONS (Future-proof)
   ============================================ */

[data-theme="light"] .media-card {
  background: var(--white);
  border-color: var(--gray-200);
}

[data-theme="light"] .media-card__info {
  background: var(--white);
}

[data-theme="light"] .media-card__title {
  color: var(--gray-900);
}

[data-theme="light"] .media-card__meta {
  color: var(--gray-600);
}
